export default "import EmblaCarousel from 'embla-carousel';\nimport { addPrevNextButtonClickHandlers } from '../../EmblaCarouselArrowButtons';\nimport { updateSelectedSnapDisplay } from '../../EmblaCarouselSelectedSnapDisplay';\nimport { addRadioFormHandler } from '../../EmblaCarouselRadioForm';\nimport { addGroupIndicatorHandler } from '../../EmblaCarouselGroupIndicator';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\nlet dynamicOptions = { ...OPTIONS };\n\nconst emblaNode = document.querySelector('.embla');\nconst viewportNode = emblaNode.querySelector('.embla__viewport');\nconst prevBtn = emblaNode.querySelector('.embla__button--prev');\nconst nextBtn = emblaNode.querySelector('.embla__button--next');\nconst snapDisplayNode = (emblaNode.querySelector('.embla__selected-snap-display'));\n\nconst showSlideGroupsFormNode = (emblaNode.querySelector('.embla__radio-form--showSlideGroups'));\nconst showSlideGroupsRadioNodes = (Array.from(showSlideGroupsFormNode.querySelectorAll('input[type=\"radio\"]')));\n\nconst slidesToScrollFormNode = (emblaNode.querySelector('.embla__radio-form--slidesToScroll'));\nconst slidesToScrollRadioNodes = (Array.from(slidesToScrollFormNode.querySelectorAll('input[type=\"radio\"]')));\n\nconst emblaApi = EmblaCarousel(viewportNode, OPTIONS);\nconst updateGroupIndicator = addGroupIndicatorHandler(emblaApi);\n\naddPrevNextButtonClickHandlers(emblaApi, prevBtn, nextBtn);\nupdateSelectedSnapDisplay(emblaApi, snapDisplayNode);\naddRadioFormHandler(showSlideGroupsFormNode, showSlideGroupsRadioNodes, updateGroupIndicator);\naddRadioFormHandler(slidesToScrollFormNode, slidesToScrollRadioNodes, (value) => {\n    dynamicOptions = {\n        ...dynamicOptions,\n        slidesToScroll: value === 'auto'\n            ? 'auto'\n            : Number(value)\n    };\n    emblaApi.reInit(dynamicOptions);\n});\n";